	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 15 – Black Eagles","texte":{"childMarkdownRemark":{"html":"<p>Un peu plus tard dans la soirée, je regarde les garçons jouer au football depuis ma fenêtre de chambre en souriant. Content de savoir que nous sommes en sécurité et que nous allons bien. Je repense à Tom et à ce qui est arrivé pour qu’il perde la vie. Sam cogne à ma porte et me regarde.</p>\n<p>Sam : Wamien... Je t’avais dit que je t’expliquerais ce qui est arrivé à Tom.<br>\nWamien : Je pensais à lui justement... J’aurais aimé pouvoir le sauver, mais je suis arrivé trop tard.<br>\nSam : Ce n’est pas de ta faute, tu sais. C’est arrivé la journée où tu t’es enfui. L’infirmière est entrée pour lui donner sa dose de calmant. Il n’en pouvait plus. Dès qu’elle est entrée dans sa chambre. Il lui a sauté dessus et l’a étouffée... Je ne pensais pas Tom être capable de choses pareilles... Alors qu’il sortait de sa chambre deux gardes lui faisait face, ayant entendu le bruit que son assassinat avait produit. Il s’est effondré devant eux, son cœur avait arrêté de battre, parce qu’il n’avait pas eu son calmant à temps.<br>\nWamien : Je... J’aurais dû être là...</p>\n<p>Sam vient mettre sa main sur mon épaule.</p>\n<p>Sam : Tu sais, même moi je n’aurais rien pu faire, tout ça est arrivé devant mes yeux. Et puis j’étais en face de lui à journée longue. Ça faisait près de 5 mois qu’il ne dormait pas, il était épuisé et il souhaitait mourir.</p>\n<p>J’en reste sans voix, mais je sais que Sam a raison, on le savait tous qu’il ne voulait qu’une chose, mourir.</p>\n<p>Sam : Écoute, maintenant nous sommes tous ici en sécurité et tout le monde va bien.</p>\n<p>Je souris un instant.</p>\n<p>Wamien : Je sais.</p>\n<p>Clara cogne à la porte.</p>\n<p>Clara : Oh, je vous dérange les garçons?<br>\nWamien : Ça va.<br>\nSam : Ouais, ça va.<br>\nClara : Hum, je voulais juste... je suis désolée pour Tom. Je ne le connaissais pas vraiment, mais c’était une bonne personne selon tes dires.<br>\nWamien : J’aurais juste aimé lui dire au revoir avant.<br>\nSam : Par contre, j’ai un souvenir de lui.</p>\n<p>Sam sortit la fameuse clé USB qu’il avait volé et que Darakei avait reprise.</p>\n<p>Wamien : Tu sais ce qu’il y a sur cette clé.<br>\nSam : Nope, mais toi tu as l’air de savoir.<br>\nWamien : Une version 2.0 de mon armure, il l’avait prise pour moi et savait que ça me concernait. On va la garder en sa mémoire question qu’il ne meurt jamais.<br>\nClara sourit : C’est touchant. Tu penses pouvoir appliquer la version 2.0?<br>\nWamien : Sans doute, toutes les données sont là pour ça.<br>\nClara : Bien, on verra ça demain. Il est déjà 22 heures.<br>\nSam : Oh come on! On va faire la fête ce soir.</p>\n<p>Clara sourit en croisant les bras.</p>\n<p>Clara : Bon, d’accord, mais ne foutez pas trop le bordel.<br>\nWamien : Pour ma part, je vais rester calme dans ma chambre.<br>\nSam : Comme tu veux super sauveur.<br>\nWamien : Meh, je n’aime pas vraiment ce surnom.<br>\nClara : Ahah, je sais que tu aimes mieux « chou » comme surnom.<br>\nWamien : Non plus</p>\n<p>Clara rit. Sam sort de ma chambre. Clara s’approche doucement vers moi.</p>\n<p>Clara : Tu veux te reposer, je suppose?<br>\nWamien : Ouais.</p>\n<p>Clara : Juste te laisser un petit truc avant d’aller dormir.</p>\n<p>Clara s’approcha et vint déposer un doux baiser sur mes lèvres. Elle rompit le baiser et sortit de ma chambre en fermant doucement la porte. « Cette femme, je pense que je suis amoureux ». Je les entends faire la fête dans le salon. Je m’allonge dans mon lit et ferme les yeux. </p>\n<p>Le lendemain matin, je me réveille, je sens de la chaleur contre moi. Je lève doucement la tête et vois Clara qui dort contre moi. Je rougis, mais ne bouge pas. Clara se réveille doucement, elle ouvre les yeux et me regarde. Elle rougit et se lève.</p>\n<p>Clara : Oh, je suis désolée. Tu dormais déjà quand je suis arrivée, je n’arrivais pas à dormir.<br>\nWamien : Je vois, tu aurais dû me réveiller.<br>\nClara : Je sais, mais il ne faut pas réveiller un homme qui dort.</p>\n<p>Elle rit et je me lève.</p>\n<p>Wamien : Je vais aller me chercher quelque chose à manger.<br>\nClara : D’accord chou.<br>\nWamien : Grrr, je n’aime pas quand tu m’appelles comme ça, tu sais?<br>\nClara : Ouais hihi.</p>\n<p>Je souris et je sors de la chambre pour aller déjeuner. Kameron est déjà debout. </p>\n<p>Wamien : Salut Kameron.<br>\nKameron se retourne vers moi : Salut Wamien, ça va?<br>\nWamien : Ouais, pas trop fais la fête hier?<br>\nKameron : Juste assez et toi? Pas trop, fais la fête avec Clara?<br>\nWamien : Quoi? Je dormais voyons, ne soit pas ridicule voyons.<br>\nKameron rigole : Ouais ouais, hey, j’ai fait des recherches avec prudence question de te rassurer.<br>\nWamien : D’accord... Vas-y.<br>\nKameron : Hier, tu disais qu’on n’était pas les seuls à avoir des pouvoirs n’est-ce pas?<br>\nWamien : Oui et?<br>\nKameron : Et bien j’ai trouvé des emplacements où des gens, qui ont supposément des pouvoirs, ont commis des crimes ou des vols.<br>\nWamien : Ils abusent de leurs pouvoirs en gros.<br>\nKameron : Ouais et si tu veux mon avis on devrait les arrêter.<br>\nWamien : Je suis d’accord, mais pas maintenant, il faudrait en premier qu’on apprenne à devenir plus forts.<br>\nKameron : Et Tim plus intelligent ahah.<br>\nWamien avec un ton de sarcasme : Très drôle Kameron, très drôle.<br>\nKameron : Roh arrête c’est juste une blague. Bref, le nom de leur groupe est Black Eagles.<br>\nWamien : Heu d’accord. Je ferais mes recherches.<br>\nKameron : Parfait.</p>\n<p>Il sourit et partit de la cuisine pour aller dans le salon.</p>\n<p>Je me fais un bol de céréales et puis je retourne dans ma chambre. Clara s’était rendormie. Je m’installe à ma table et mange en silence en la regardant de temps en temps. Après avoir mangé, je prends le portable de Clara, pirate le mot de passe et va rechercher sur Internet le fameux groupe « Black Eagles ». Vol de banque, extorsion et meurtre. Beaucoup d’événements sont arrivés en leur nom, ce qui est peu rassurant. Un moyen pour les reconnaître est qu’ils portent un écusson d’aigle en plein vol sur l’épaule gauche. C’est bon à savoir. </p>"}},"chapitreAvant":"chapitre-14-les-presentations","chapitreApres":"chapitre-16-le-reperage","nomRoman":"../le-premier-cyborg-tome-1","slug":"chapitre-15-black-eagles"}},"pathContext":{"slug":"chapitre-15-black-eagles"}}

